.move-free {

  @include pageGrid;

  &__title {
    grid-column: 1 / -1;
    text-align: left;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__content {
    color: $text-main;
    font-size: 23px;
    font-weight: 500;
    line-height: 140%;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: 6 / 12;
    }
  }
}
